<template>
    <el-dialog title="" class="upload__dialog" :visible="value" @close="$emit('close')">
        <el-tabs type="card" :value="'upload'">
            <el-tab-pane class="upload-panel" label="" name="upload">
                <el-select v-model="imgHost" @change="changeImgHost" placeholder="" size="small">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
                <el-upload drag action :headers="{'Content-Type': 'multipart/form-data'}" :show-file-list="false"
                    :multiple="true" accept=".jpg, .jpeg, .png, .gif" name="file" :before-upload="beforeUpload"
                    v-loading="uploadingImg">
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">
                        
                        <em></em>
                    </div>
                </el-upload>
            </el-tab-pane>
            <el-tab-pane class="github-panel" label="GitHub " name="github">
                <el-form class="setting-form" ref="form" :model="formGitHub" label-position="right" label-width="140px">
                    <el-form-item label="GitHub " :required="true">
                        <el-input v-model.trim="formGitHub.repo" placeholder="github.com/yanglbme/resource"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model.trim="formGitHub.branch" placeholder="release master"></el-input>
                    </el-form-item>
                    <el-form-item label="Token" :required="true">
                        <el-input v-model.trim="formGitHub.accessToken" show-password
                            placeholder="cc1d0c1426d0fd0902bd2d7184b14da61b8abc46"></el-input>
                        <el-link type="primary"
                            href="https://docs.github.com/en/github/authenticating-to-github/creating-a-personal-access-token"
                            target="_blank"> GitHub Token</el-link>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="saveGitHubConfiguration"></el-button>
                    </el-form-item>
                </el-form>
            </el-tab-pane>
             <el-tab-pane class="github-panel" label=" OSS" name="aliOSS">
                <el-form class="setting-form" ref="form" :model="formAliOSS" label-position="right" label-width="140px">
                    <el-form-item label="AccessKey ID" :required="true">
                        <el-input v-model.trim="formAliOSS.accessKeyId" placeholder="LTAI4GdoocsmdoxUf13ylbaNHk"></el-input>
                    </el-form-item>
                    <el-form-item label="AccessKey Secret" :required="true">
                        <el-input v-model.trim="formAliOSS.accessKeySecret" show-password
                            placeholder="cc1d0c142doocs0902bd2d7md4b14da6ylbabc46"></el-input>
                    </el-form-item>
                    <el-form-item label="Bucket" :required="true">
                        <el-input v-model.trim="formAliOSS.bucket"
                            placeholder="doocs"></el-input>
                    </el-form-item>
                    <el-form-item label="Bucket " :required="true">
                        <el-input v-model.trim="formAliOSS.region"
                            placeholder="oss-cn-shenzhen"></el-input>
                    </el-form-item>
                     <el-form-item label="">
                        <el-input v-model.trim="formAliOSS.path"
                            placeholder="img"></el-input>
                        <el-link type="primary"
                            href="https://help.aliyun.com/document_detail/31883.html"
                            target="_blank"> OSS</el-link>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="saveAliOSSConfiguration"></el-button>
                    </el-form-item>
                </el-form>
            </el-tab-pane>
            <el-tab-pane class="github-panel" label=" COS" name="txCOS">
                <el-form class="setting-form" ref="form" :model="formTxCOS" label-position="right" label-width="140px">
                    <el-form-item label="SecretId" :required="true">
                        <el-input v-model.trim="formTxCOS.secretId" placeholder="AKIDnQp1w3DOOCSs8F5MDp9tdoocsmdUPonW3"></el-input>
                    </el-form-item>
                    <el-form-item label="SecretKey" :required="true">
                        <el-input v-model.trim="formTxCOS.secretKey" show-password
                            placeholder="ukLmdtEJ9271f3DOocsMDsCXdS3YlbW0"></el-input>
                    </el-form-item>
                    <el-form-item label="Bucket" :required="true">
                        <el-input v-model.trim="formTxCOS.bucket"
                            placeholder="doocs-3212520134"></el-input>
                    </el-form-item>
                    <el-form-item label="Bucket " :required="true">
                        <el-input v-model.trim="formTxCOS.region"
                            placeholder="ap-guangzhou"></el-input>
                    </el-form-item>
                     <el-form-item label="">
                        <el-input v-model.trim="formTxCOS.path"
                            placeholder="img"></el-input>
                        <el-link type="primary"
                            href="https://cloud.tencent.com/document/product/436/38484"
                            target="_blank"> COS</el-link>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="saveTxCOSConfiguration"></el-button>
                    </el-form-item>
                </el-form>
            </el-tab-pane>
        </el-tabs>
    </el-dialog>
</template>

<script>
import {
    uploadImgFile
} from "../../assets/scripts/uploadImageFile";

export default {
    props: {
        value: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            formGitHub: {
                repo: "",
                branch: "",
                accessToken: "",
            },
            formAliOSS: {
                accessKeyId: "",
                accessKeySecret: "",
                bucket: "",
                region: "",
                path: ""
            },
            formTxCOS: {
                secretId: "",
                secretKey: "",
                bucket: "",
                region: "",
                path: ""
            },
            options: [{
                    value: "default",
                    label: "",
                },
                {
                    value: "github",
                    label: "GitHub",
                },
                {
                    value: "aliOSS",
                    label: ""
                },
                {
                    value: "txCOS",
                    label: ""
                }
            ],
            imgHost: "default",
            uploadingImg: false,
        };
    },
    created() {
        if (localStorage.getItem("githubConfig")) {
            this.formGitHub = JSON.parse(localStorage.getItem("githubConfig"));
        }
        if (localStorage.getItem("aliOSSConfig")) {
            this.formAliOSS = JSON.parse(localStorage.getItem("aliOSSConfig"));
        }
        if (localStorage.getItem("txCOSConfig")) {
            this.formTxCOS = JSON.parse(localStorage.getItem("txCOSConfig"));
        }
        if (localStorage.getItem("imgHost")) {
            this.imgHost = localStorage.getItem("imgHost");
        }
    },
    methods: {
        changeImgHost() {
            localStorage.setItem("imgHost", this.imgHost);
            this.$message({
                showClose: true,
                message: '',
                type: "success",
            });
        },
        saveGitHubConfiguration() {
            if (!(this.formGitHub.repo && this.formGitHub.accessToken)) {
                const blankElement = this.formGitHub.repo ? "token" : "GitHub "
                this.$message({
                    showClose: true,
                    message: `${blankElement}`,
                    type: "error",
                });
                return;
            }
            localStorage.setItem("githubConfig", JSON.stringify(this.formGitHub));
            this.$message({
                message: "",
                type: "success",
            });
        },
        saveAliOSSConfiguration() {
            if (!(this.formAliOSS.accessKeyId && this.formAliOSS.accessKeySecret && this.formAliOSS.bucket && this.formAliOSS.region)) {
                this.$message({
                    showClose: true,
                    message: ` OSS `,
                    type: "error",
                });
                return;
            }
            localStorage.setItem("aliOSSConfig", JSON.stringify(this.formAliOSS));
            this.$message({
                message: "",
                type: "success",
            });
        },

        saveTxCOSConfiguration() {
            if (!(this.formTxCOS.secretId && this.formTxCOS.secretKey && this.formTxCOS.bucket && this.formTxCOS.region)) {
                this.$message({
                    showClose: true,
                    message: ` COS `,
                    type: "error",
                });
                return;
            }
            localStorage.setItem("txCOSConfig", JSON.stringify(this.formTxCOS));
            this.$message({
                message: "",
                type: "success",
            });
        },

        // 
        beforeUpload(file) {
            if (!this.validateConfig()) {
                return;
            }

            this.uploadingImg = true;
            uploadImgFile(file)
                .then(res => {
                    this.$emit("uploaded", res);
                    this.uploadingImg = false;
                })
                .catch(err => {
                    this.uploadingImg = false;
                    this.$message({
                        showClose: true,
                        message: err,
                        type: "error",
                    });
                });
            return false;
        },
        validateConfig() {
            switch (localStorage.getItem('imgHost')) {
                case "github":
                    if (!(this.formGitHub.repo && this.formGitHub.accessToken)) {
                        this.$message.error(" GitHub ");
                        return false;
                    }
                    break;
                case 'aliOSS':
                    if (!(this.formAliOSS.accessKeyId && this.formAliOSS.accessKeySecret && this.formAliOSS.bucket && this.formAliOSS.region)) {
                        this.$message.error(" OSS ");
                        return false;
                    }
                    break;
                case 'txCOS':
                    if (!(this.formTxCOS.secretId && this.formTxCOS.secretKey && this.formTxCOS.bucket && this.formTxCOS.region)) {
                        this.$message.error(" COS ");
                        return false;
                    }
                    break;
                default:
                    return true;
            }
            return true;
        },
    },
};
</script>

<style lang="less" scoped>
/deep/ .el-dialog {
    width: 40%;
}
/deep/ .el-upload-dragger {
    width: 335px;
}
/deep/ .el-dialog__body {
    padding-bottom: 50px;
}
.upload-panel {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;

    .el-select {
        align-self: flex-end;
        margin: 0 67.75px 20px;
        width: 100px;
    }
}

.github-panel {
    display: flex;
    justify-content: center;
}

.setting-form {
    width: 100%;

    .el-form-item {
        margin: 15px;
    }

    .el-form-item:last-child {
        text-align: right;
    }
}

</style>
