<template>
    <el-dialog title="" class="reset__dialog" :visible="showResetConfirm" @close="$emit('close')">
        <div class="text">
            ?
        </div>
        <div slot="footer" class="dialog-footer">
            <el-button :type="btnType" plain @click="$emit('close')"> </el-button>
            <el-button :type="btnType" @click="$emit('confirm')" plain> </el-button>
        </div>
    </el-dialog>
</template>

<script>
import {mapState} from 'vuex';
export default {
    props: {
        showResetConfirm: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        btnType() {
            return this.nightMode ? 'default' : 'primary';
        },
        ...mapState({
            nightMode: state => state.nightMode
        })
    }
}
</script>

<style lang="less" scoped>
.reset__dialog {
    text-align: center;
}
.text {
    text-align: center;
}
.dialog-footer {
    text-align: center;
}
</style>